body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
  min-height: 10vh;
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  border-bottom: 2px solid #8899ab; }

.left-links {
  position: absolute;
  left: 10px;
  bottom: 0px; }

a {
  font-size: 1rem;
  font-weight: 300;
  margin: 10px;
  text-decoration: none; }

.favicon {
  height: 40px;
  position: absolute;
  bottom: -3px; }

.App {
  text-align: center; }

body {
  font-family: 'Signika', sans-serif; }

.text-block {
  margin-top: 6rem; }

p {
  font-weight: 300; }

